.wrapper {
  width: 100%;
  position: absolute;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  display: grid;
  place-items: center;
  grid-gap: 15px;
  gap: 15px;
}

.marginright {
  margin-right: "100px";
}

.nitrocontainer {
  height: 400px;
  width: 200px;
  border-radius: 8px;
  position: relative;
  cursor: pointer;
  color: #ffffff;
  background: radial-gradient(var(--theme-color-1), var(--theme-color-2));
  /* box-shadow: 0px 1px 10px #ffffff; */
}

.nftcontainer {
  height: 230px;
  width: 100px;
  border-radius: 8px;
  position: relative;
  cursor: pointer;
  color: #ffffff;
  background: radial-gradient(var(--theme-color-1), var(--theme-color-2));
  box-shadow: 0px 1px 10px #ffffff;
}

input[type="checkbox"] {
  -webkit-appearance: none;
  position: relative;
  width: 100%;
  cursor: pointer;
}

input[type="checkbox"]:after {
  position: absolute;
  font-family: "Font Awesome 5 Free";
  font-weight: 400;
  content: "\F111";
  font-size: 18px;
  color: #ffffff;
  right: 10px;
  top: -5px;
}

input[type="checkbox"]:checked:after {
  font-weight: 900;
  content: "\F058";
  color: #ffffff;
}

.nftcontainer img {
  width: 70%;
  position: absolute;
  margin: auto;
  left: 0;
  right: 0;
  top: 15%;
  cursor: pointer;

}

.nftcontainer-mobile img {
  width: 70%; 
  /* position: absolute; */
  margin: auto;
  left: 0;
  right: 0;
  top: 15%;
  cursor: pointer;

}

@media screen and (min-width: 950px) {
  .wrapper {
    grid-template-columns: repeat(5, 1fr);
    grid-gap: 10px;
    gap: 10px;
  }

  .nftcontainer {
    height: 300px;
    width: 200px;
  }
  
  .nftcontainer-mobile {
    height: 300px;
    width: 200px;
  }

  input[type="checkbox"]:after {
    font-size: 22px;
  }
}

.nftdetail {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 5%;
  cursor: pointer;
  display: grid;
