.vault_container:before {
  content: "";
  position: absolute;
  height: 280px;
  width: 280px;
  background: radial-gradient(var(--theme-color-1), var(--theme-color-2));
  border-radius: 50%;
  top: 100%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  margin: 50%;
}

.vault_card {
  height: 285px;
  width: 200px;
  position: relative;
  box-shadow: 0px 1px 10px #fafae6;
  border-radius: 5%;
  border: 10px solid var(--white-rgba);
}

.vault_front,
.vault_back {
  position: absolute;
  display: flex;
  align-items: center;
  height: 100%;
  width: 100%;
  border-radius: 15px;
  background-color: var(--white-rgba);
  border: 2px solid var(--white-rgba);
  -webkit-backdrop-filter: blur(8px);
          backdrop-filter: blur(8px);
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  transition: 1s;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);
}

.vault_front {
  flex-direction: column;
  justify-content: center;
  grid-gap: 50px;
  gap: 50px;
}

.vault_front img {
  height: 100px;
  width: 100px;
  border-radius: 50%;
  border: 10px solid var(--white-rgba);
}

.vault_details {
  text-align: center;
}

h2 {
  font-size: 22px;
